<template>
  <div>
    <div class="bkground">
      <section class="head searchHead clearfix">
        <div class="backWrap">
          <a @click="goBack" class="iconfont back">&#xe614;</a>
        </div>
        <div class="searchHeadRight">
          <div class="noticeWrap">
             <span class="bag"> <em class="font10"> 3 </em> </span>
            <a
              href="javascript:void(0)"
              @click="$router.push('/notice')"
              class="iconfont notice"
            >
              &#xe66b;
            </a>
          </div>
        </div>
      </section>
      <div class="shareWrap">
        <div class="information">
          <div class="photo" @click="handlerPhoto">
            <img :src="myStore.headImage" />
          </div>
          <div v-show="false">
            <input
              type="file"
              fileTypes="image/png,image/jpeg,image/gif,image/jpg"
              ref="headImage"
              @change="uploadFile"
            />
          </div>
          <div class="name">
            <p>
              {{ myStore.userName }}
              <a @click="$router.push('editProfile')" class="iconfont back">&#xe654;</a>
            </p>
            <span>
              Invitation Code:
              <i>{{ myStore.invitationCode }}</i>
            </span>
          </div>
          <div class="share" @click="$router.push('ads')">
              <div>
                <span>Share</span>
                <span class="iconfont blue">&#xe93d;</span>
              </div>
          </div>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="totalRevenue innerItem">
        <p>
          {{ myStore.totalRevenue || "0.00" }}
          <a @click="$router.push('comisson')" class="iconfont blue"
            >&#xe93d;</a
          >
        </p>
        <span>Total revenue</span>
        <div class="balanceCash">
          <div class="balance">
            <p>{{ myStore.balance || "0.00" }}</p>
            <span>Balance(AED)</span>
          </div>
          <div class="dividing">
            <div></div>
          </div>
          <div class="cash">
            <p>{{ myStore.cash || "0.00" }}</p>
            <span>Cash(AED)</span>
            <span class="withdraw" @click="$router.push('withdraw')">Withdraw</span>
          </div>
        </div>
      </div>
      <div class="orderProfit innerItem">
        <div class="orders">
          <img src="../common/images/generalOrder.png" />
          <div class="count">
            <p>{{ myStore.orders || "0" }}</p>
            <span>Orders</span>
          </div>
        </div>
        <div class="dividing2">
          <div></div>
        </div>
        <div class="yesterday">
          <img src="../common/images/yesterdayEarnings.png" />
          <div class="count">
            <p>{{ myStore.yesterdayRevenue || "0.00" }}</p>
            <span>Yesterday's Revenue</span>
          </div>
        </div>
      </div>
      <div class="customersInvited innerItem">
        <div class="myInvited">
          <div class="title">
            <span class="iconfont gray">&#xe658;</span>
            <p>Customers I Invited</p>
          </div>
          <div class="decollate">
            <img src="../common/images/division.png" />
          </div>
        </div>
        <div class="invitedCount">
          <p>
            Yesterday Invited:
            <i>{{ myStore.yesterdayInvited || "0" }}</i>
          </p>
          <p>
            Cumulative Invited:
            <i>{{ myStore.cumulativeInvited || "0" }}</i>
          </p>
        </div>
      </div>
      <div class="decorative innerItem">
        <p>Decorative arts and crafts</p>
        <span>UP TO 60% OFF</span>
      </div>
    </div>
  </div>
</template>

<script>
import { myStore, updateImg } from "@/api/store";
import { mapGetters } from "vuex";
export default {
  name: "myStores",
  data() {
    return {
      myStore: {}
    };
  },
  created() {
    myStore().then(res => {
      this.myStore = res.data.result;
    });

    console.log(this.user);
  },
  computed: {
    ...mapGetters(["user", "indexData"])
  },
  methods: {
    confirm() {
      this.$dialog
        .confirm({
          message: "Confirm to Log Out?",
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          this.$store.dispatch("logout");
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    handlerPhoto() {
      this.$refs.headImage.click();
    },
    uploadFile() {
      let files = this.$refs.headImage.files;
      if (files.length == 0) {
        return false;
      }
      let param = new FormData();
      param.append("file", files[0]);
      updateImg(param).then(res => {
        myStore().then(res => {
          this.myStore = res.data.result;
        });
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.bkground {
  background: url("../common/images/layer39.png") no-repeat;
  background-size: cover;
  width: 100%;
  height: 222px;
}
.backWrap {
  float: left;
  width: 25px;
  line-height: 42px;
}
.searchHeadRight {
  width: 35px;
  float: right;
  text-align: right;
  line-height: 42px;
}
.head {
  display: block;
  background: none;
}
.head .iconfont {
  color: white;
}
.shareWrap {
  width: 100%;
  padding-top: 22px;
  padding-left: 4%;
  overflow: hidden;
  .information {
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    .photo {
      margin-right: 10px;
      img {
        height: 58px;
        width: 58px;
      }
    }
    .name {
      p {
        color: #fff;
        font-size: 18px;
      }
      span {
        color: #fff;
        font-size: 12px;
      }
    }
    .share {
      position: absolute;
      height: 28px;
      right:0;
      width:85px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: url("../common/images/shareBg.png") no-repeat;
      background-size:auto 28px;
      &>div>*{line-height:14px; display: inline-block;}
      img {
        height: 100%;
      }
      span {
        font-size: 14px;
        font-weight: 500;
      }
      .iconfont {
        font-size: 12px;
      }
    }
  }
}
.center {
  width: 100%;
  background-color: #f6f6f6;
  .totalRevenue {
    margin: 0 15px;
    background-color: #fff;
    border-radius: 5px;
    p {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 22px;
      color: #e33946;
      padding-top: 12px;
      a {
        font-size: 12px;
      }
    }
    span {
      font-size: 12px;
      color: #666666;
      display: block;
      text-align: center;
    }
  }
  .balanceCash {
    display: -webkit-box;
    padding-bottom: 15px;
    border-radius: 5px;
    .balance {
      width: 49%;
      p {
        font-size: 17px;
        color: #000;
      }
      span {
        font-size: 12px;
      }
    }
    .cash {
      width: 49%;
      p {
        font-size: 17px;
        color: #000;
      }
      span {
        font-size: 12px;
      }
      .withdraw {
        color: #1f89d3;
      }
    }
  }
  .orderProfit {
    background: url("../common/images/myStores.png") no-repeat;
    background-size: 100% 100%;
    margin: 0 10px;
    display: -webkit-box;
    .orders {
      width: 49%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 30px 0;
      img {
        width: 29px;
        height: 29px;
      }
      .count {
        margin-left: 15px;
        p {
          color: #333333;
          font-size: 17px;
        }
        span {
          font-size: 12px;
          color: #666666;
        }
      }
    }
    .yesterday {
      width: 49%;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 26px;
        height: 29px;
      }
      .count {
        margin-left: 10px;
        p {
          color: #333333;
          font-size: 17px;
        }
        span {
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }
  .customersInvited {
    width: 100%;
    height: 106px;
    background-color: #fff;
    display: -webkit-box;
    .myInvited {
      width: 50%;
      display: -webkit-box;
      .title {
        padding-left: 20px;
        padding-right: 5px;
        span {
          font-size: 23px;
          padding-top: 30px;
        }
        p {
          color: #333333;
          font-size: 14px;
          padding-top: 10px;
        }
        .iconfont {
          display: flex;
          justify-content: center;
        }
      }
      .decollate {
        img {
          height: 96px;
          margin-top: 5px;
        }
      }
    }
    .invitedCount {
      width: 50%;
      p {
        color: #999999;
        font-size: 13px;
        padding-top: 22px;
        i {
          font-size: 15px;
          color: #666666;
        }
      }
    }
  }
  .decorative {
    width: 100%;
    height: 131px;
    background: url("../common/images/decorative.png") no-repeat;
    background-size: cover;
    p {
      width: 100%;
      font-size: 17px;
      color: #ffffff;
      text-align: center;
      padding-top: 33px;
    }
    span {
      width: 100%;
      font-size: 17px;
      color: #ffffff;
      text-align: center;
      display: block;
      padding-top: 10px;
    }
  }

.innerItem{ position: relative; top:-45px; margin-bottom: 10px;}

}
.dividing {
  height: 60px;
  display: flex;
  align-items: flex-end;
  div {
    height: 62%;
    border-right: #cecece 1px solid;
  }
}
.dividing2 {
  height: 82px;
  display: flex;
  align-items: flex-end;
  div {
    height: 80%;
    border-right: #cecece 1px solid;
  }
}
</style>



